import { isTagEmbed, isSocialEmbed } from "../../../../actions/api";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
import Like from "../../Elements/Reactions/Like";
const Media = ({ itemData, wallID, ownerId, wall }) => {
  const autoPlayTagEmbed = isTagEmbed && (itemData.type === 3 || itemData.type === 5) && !wall.Personalization.mobilePopup && !wall.Personalization.postFeatured ? true : false
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbed ? wall.ThemeRule.aspectImageRatio ? wall.ThemeRule.aspectImageRatio : false : false


  return <div className="tb_cc_media_wrap">
    {!autoPlayTagEmbed ? <MediaIcon itemData={itemData} IconClass="tb_cc_media_icon" show={true} isCenter={isTagEmbed} /> : null}
    {isSocialEmbed ? <Like postId={itemData.id} /> : null}
    {autoPlay || autoPlayTagEmbed ?
      <Video VideoClass="tb_cc_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} isCover={false} controls={true} autoPlay={false} muted={true} />
      :
      <Image ImageClass="tb_cc_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} ThemeRule={wall.ThemeRule} />
    }
  </div>
}
export default Media