import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
import Network from "../../Elements/Network/Network";
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  return <div className="tb_cc_author_wrapper">
    <div className="tb_cc_author">

      {postAuthor ? <Author author={author} network={network} authorClass={'tb_cc_author_profile'} /> : ""}

      <div className="tb_cc_author_info">
        {postAuthor ? <div className="tb_cc_authorname">{author.name}</div> : ''}
        <div className="tb_cc_post_info">
          {postAuthor ? <div className="tb_cc_username">{username}</div> : ''}
          {authorSeprator ? <div className="tb_cc_seprator"> </div> : ''}
          {personalization.postTime ? <Time postTime={postTime} timeClass={`tb_cc_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /> : ''}
        </div>
      </div>
    </div>
    <div className="tb_cc_social_">
      <Network networkClass={'tb_cc_social_ico'} network={network} ThemeRule={ThemeRule} isDefault={ThemeRule.iconType === 1 ? true : false} />
    </div>
  </div>
}
export default AuthorDetails